import React from "react";
import axios from "axios";
import Movie from "../components/Movie";
import "./Home.css";

class ListDC extends React.Component {
  state = {
    isLoading: true,
    movies: []
  };
  getMovies = async () => {
    let url = "https://yts.mx/api/v2/list_movies.json"
    url += "?sort_by=download_count"
    // if (this.state.sortby !== undefined) {
    //   url ="https://yts.mx/api/v2/list_movies.json?sort_by=" + this.state.sortby
    // }

    const {
      data: {
        data: { movies }
      }
    } = await axios.get(
      url
    );
    this.setState({ movies, isLoading: false });
  };
  componentDidMount() {
    // const { location, history } = this.props;
    // if (location.state === undefined) {
    //   history.push("/");
    // }

    this.getMovies();
  }
  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
        ) : (
          <div className="movies">
            {movies.map(movie => (
              <Movie
                key={movie.id}
                id={movie.id}
                year={movie.year}
                title={movie.title}
                summary={movie.summary}
                poster={movie.medium_cover_image}
                genres={movie.genres}
              />
            ))}
          </div>
        )}
      </section>
    );
  }
}

export default ListDC;
